@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
a.home-option{color:inherit;text-decoration:inherit}

.home{background:#FFD029;height:1080px;color:white;display:flex;justify-content:center}.home div{flex-grow:1;margin:0 50px;display:flex;justify-content:space-between;align-items:center}


.video{border:4px solid #FFD029}.video iframe{position:relative;margin-bottom:-30px;margin-right:-30px;padding:10px}

.external-videos{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:30px;gap:30px}



.content{font-size:18px}.content-wrap{width:85%;margin:0 auto}

html{scroll-behavior:smooth}body{margin:0;font-family:'Varela', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-size:48px;font-weight:400;line-height:1}

